<template>
  <div class="login-container">
    <div class="login_box">
      <!--  -->
      <div class="logo_box">
        <img src="../../assets/images/avatar.png" alt="logo" />
      </div>
      <!--  -->
      <div class="login_title"></div>
      <!--  -->
      <el-form
        label-width="0px"
        ref="loginRef"
        :model="loginForm"
        :rules="loginRules"
        class="login_form"
      >
        <!--  -->
        <el-form-item prop="username">
          <el-input v-model="loginForm.username" prefix-icon="iconfont icon-yonghu"></el-input>
        </el-form-item>
        <!--  -->
        <el-form-item prop="password">
          <el-input v-model="loginForm.password" prefix-icon="iconfont icon-lock" show-password></el-input>
        </el-form-item>
        <!--  -->
        <el-form-item class="btns">
          <el-button type="primary" icon="el-icon-lollipop" @click="login"></el-button>
          <el-button type="info" icon="el-icon-refresh-right" @click="resetLogin"></el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // 
      loginForm: {
        username: '',
        password: ''
      },
      // 
      loginRules: {
        // 
        username: [
          { required: true, message: '', trigger: 'blur' },
          { min: 3, max: 10, message: ' 3  10 ', trigger: 'blur' }
        ],
        // 
        password: [
          { required: true, message: '', trigger: 'blur' },
          { min: 3, max: 18, message: ' 6  15 ', trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    // 
    resetLogin() {
      // console.log(this);
      this.$refs.loginRef.resetFields()
    },
    login() {
      this.$refs.loginRef.validate(async valid => {
        // console.log(valid);
        const path = this.URL.Login
        console.log(path)
        if (!valid) return
        // {data:res} ES6.0 
        const { data: res } = await this.$http
          .post(path, this.loginForm)
          .catch(() => {
            this.$message.error('NETWORK ERROR')
          })
        if (res.status !== 200) return this.$message.error('')
        // 
        this.$message.success('')

        // 1.tokensessionStorage
        // token
        // window.sessionStorage.setItem('token');
        //  
        window.localStorage.setItem('admin_token', this.loginForm.username)
        this.$router.push('/home')
      })
    }
  }
}
</script>

<style lang="less" scoped>
.login-container {
  background-color: #21374b;
  height: 100%;
}

.login_title {
  font-size: 25px;
  font-family: Arial, Helvetica, sans-serif;
  color: orange;
  // letter-spacing: 1px;
  position: absolute;
  top: 12%;
  left: 50%;
  transform: translate(-50%);
}

.login_box {
  width: 450px;
  height: 300px;
  background-color: #fff;
  border-radius: 10px;
  // 
  box-shadow: 0 0 20px whitesmoke;
  // login
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);

  // logo
  .logo_box {
    height: 110px;
    width: 110px;
    // border: 1px solid #eee;
    // border-radius: 50%;
    // padding: 10px;
    // box-shadow: 0 0 10px antiquewhite;

    // 
    position: absolute;
    left: 50%;
    transform: translate(-50%, -100%);
    // image
    img {
      // 
      width: 100%;
      height: 100%;
      // border-radius: 50%;
    }
  }
}

.login_form {
  position: absolute;
  bottom: 0;
  width: 100%;
  padding: 0 20px;
  box-sizing: border-box;
}

.btns {
  display: flex;
  justify-content: flex-end;
}
</style>