import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../components/common/Home.vue'
import Login from '../components/blocks/Login.vue'
import Welcome from '../components/blocks/Welcome.vue'
import Order from '../components/blocks/Order.vue'
import Foods from '../components/blocks/Foods.vue'
import Cover from '../components/blocks/Cover.vue'
import Member from '../components/blocks/Member.vue'
import Business from '../components/blocks/Business.vue'
import AddFoods from '../components/blocks/AddFoods'
import EditFoods from '../components/blocks/EditFoods'
import ViewFoods from '../components/blocks/ViewFoods'
import Category from '../components/blocks/Category'


Vue.use(VueRouter)

const routes = [{
        path: '/',
        redirect: '/login'
    },
    {
        path: '/home',
        name: 'Home',
        component: Home,
        redirect: '/welcome',
        meta: [''],
        children: [{
                path: '/welcome',
                component: Welcome,
                meta: ['']
            },
            {
                path: '/order',
                name: 'Order',
                component: Order,
                meta: ['', ''],
            },
            {
                path: '/foods',
                name: 'Foods',
                component: Foods,
                meta: ['', ''],
            },
            {
                path: '/addfoods',
                name: 'AddFoods',
                component: AddFoods,
                meta: ['', ''],
            },
            {
                path: '/viewfoods',
                name: 'ViewFoods',
                component: ViewFoods,
                meta: ['', '', ''],
            },
            {
                path: '/category',
                name: 'Category',
                component: Category,
                meta: ['', ''],
            },
            {
                path: '/editfoods',
                name: 'EditFoods',
                component: EditFoods,
                meta: ['', '', ''],
            },
            {
                path: '/cover',
                component: Cover,
                meta: ['', ''],
            },
            {
                path: '/member',
                component: Member,
                meta: ['', ''],
            },
            {
                path: '/business',
                component: Business,
                meta: ['', ''],
            }
        ]
    }, {
        path: '/login',
        name: 'Login',
        component: Login
    }
]

const router = new VueRouter({
    routes,
    // mode: 'history',
    // base: process.env.BASE_URL,
})

// 
router.beforeEach((to, from, next) => {
    // to
    // from
    // next  
    // next()  next('/login) 
    if (to.path === '/login') return next();
    // token
    const tokenStr = window.localStorage.getItem('admin_token');
    if (!tokenStr) return next('/login');
    next();
})

export default router