<template>
  <div>
    <!--  -->
    <el-card shadow="hover">
      <el-row :gutter="5">
        <!--  -->
        <el-col :span="6">
          <el-input
            placeholder=""
            v-model="queryInfo.query"
            @clear="getCategoryList"
            clearable
          ></el-input>
        </el-col>
        <!--  -->
        <el-col :span="4">
          <el-button type="primary" @click="getCategoryList"></el-button>
        </el-col>
      </el-row>
    </el-card>
    <!--  -->
    <el-table
      :data="res"
      border
      :row-class-name="tableRowClassName"
      size="mini"
      :header-cell-style="{background:'#ffd0bd',color:'#606266'}"
      v-loading="loading"
    >
      <el-table-column type="index" label="#" align="center"></el-table-column>
      <el-table-column prop="name" label="" align="center"></el-table-column>
      <el-table-column prop="updated_time" label="" align="center"></el-table-column>
      <el-table-column prop="created_time" label="" align="center"></el-table-column>
      <el-table-column label="" width="145%" align="center">
        <template slot-scope="scope">
          <el-button size="mini" @click="handleEdit(scope.row)"></el-button>
          <el-button size="mini" type="danger" @click="handleDelete(scope.row.id)"></el-button>
        </template>
      </el-table-column>
    </el-table>

    <!-- | -->
    <el-dialog title="" :visible.sync="editVisible">
      <el-form :model="editForm" >
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="editForm.name" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="editVisible = false"> </el-button>
        <el-button type="primary" @click="saveEdit"> </el-button>
      </div>
    </el-dialog>

    <!--  -->
    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="queryInfo.pagenum"
      :page-sizes="[10, 20]"
      :page-size="queryInfo.pagesize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="total"
    ></el-pagination>
  </div>
</template>

<style>
.el-table .warning-row {
  background: oldlace;
}

.el-table .success-row {
  background: #f0f9eb;
}
</style>

<script>
export default {
  data() {
    return {
      // 
      queryInfo: {
        query: '',
        // 
        pagenum: 1,
        // 
        pagesize: 10
      },
      editVisible: false,
      editForm: {},
      formLabelWidth: '80px',
      res: [],
      total: 0,
      loading: true
    }
  },
  methods: {
    tableRowClassName({ rowIndex }) {
      if (rowIndex === 1) {
        return 'warning-row'
      } else if (rowIndex === 3) {
        return 'success-row'
      }
      return ''
    },
    async getCategoryList() {
      this.loading = true
      const path = this.URL.Categories
      let temp = []
      const { data: result } = await this.$http
        .get(path, {
          params: this.queryInfo
        })
        .catch(() => {
          this.$message.error('NETWORK ERROR')
        })
      this.loading = false
      console.log(result)
      result.categories.forEach(element => {
        temp.push(JSON.parse(element))
      })
      this.res = temp
      this.total = result.total
      console.log(this.res)
    },
    //  pagesize 
    handleSizeChange(newSize) {
      console.log(newSize)
      this.queryInfo.pagesize = newSize
      // 
      this.getCategoryList()
    },
    //   
    handleCurrentChange(newPage) {
      console.log(newPage)
      this.queryInfo.pagenum = newPage
      // 
      this.getCategoryList()
    },
    // 
    handleEdit(row) {
      this.editVisible = true
      this.editForm = row
    },
    // 
    async saveEdit() {
      this.editVisible = false
      const path = this.URL.Categories
      const { data: res } = await this.$http
        .patch(path, {
          data: {
              ...this.editForm
          }
        })
        .catch(() => {
          this.$message.error('NETWORK ERROR')
        })
      if (res.status !== 200) {
        this.$message.error('')
      }
      this.$message.success('')
    },
    // 
    async handleDelete(id) {
      const confirmResult = await this.$confirm(
        ', ?',
        '',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }
      ).catch(() => {
        this.$message.error('NETWORK ERROR')
      })

      if (confirmResult !== 'confirm') {
        return this.$message.info('')
      }
      console.log(confirmResult, id)

      // delete
      const path = this.URL.Categories
      const { data: res } = await this.$http
        .delete(path, {
          params: { id: id }
        })
        .catch(() => {
          this.$message.error('NETWORK ERROR')
        })
      if (res.status !== 200) {
        this.$message.error('')
      }
      this.$message.success('')
      this.getCategoryList()
    },
    // 
    formatSex(row) {
      return row.sex === 1 ? '' : ''
    }
  },
  created() {
    this.getCategoryList()
  }
}
</script>

<style lang="less" scoped>
.state {
  text-align: center;
}
</style>