<template>
  <div>
    <!--  -->
    <el-card shadow="hover">
      <el-row :gutter="5">
        <!--  -->
        <el-col :span="4">
          <el-select
            v-model="queryCategory.query"
            placeholder=""
            @change="filterbyCategory"
            @clear="getFoodsList"
            clearable
          >
            <el-option
              v-for="item in categoryForm.categoryList"
              :key="item.id"
              :label="item.name"
              :value="item.name"
            ></el-option>
          </el-select>
        </el-col>
        <el-col :span="4">
          <el-select
            v-model="queryStatus.query"
            placeholder=""
            @change="filterbyStatus"
            @clear="getFoodsList"
            clearable
          >
            <el-option
              v-for="item in options"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
        </el-col>
        <!--  -->
        <el-col :span="6">
          <el-input placeholder="" v-model="queryInfo.query" @clear="getFoodsList" clearable></el-input>
        </el-col>
        <!--  -->
        <el-col :span="1">
          <el-button type="primary" icon="el-icon-search" @click="getFoodsList"></el-button>
        </el-col>
        <!--  -->
        <el-col :span="2" :offset="7">
          <el-button icon="el-icon-plus" @click="addFoods" plain></el-button>
        </el-col>
      </el-row>
    </el-card>

    <!--  -->
    <el-table
      :data="res"
      border
      fit
      :row-class-name="tableRowClassName"
      size="mini"
      :header-cell-style="{background:'#eed2d8',color:'#606266'}"
      v-loading="loading"
    >
      <el-table-column type="index" label="#" align="center"></el-table-column>
      <el-table-column prop="imageURL" label="" width="61%" align="center">
        <template slot-scope="scope">
          <el-avatar :src="scope.row.imageURL" icon="el-icon-user-solid" shape="square"></el-avatar>
        </template>
      </el-table-column>
      <el-table-column prop="category" label="" align="center"></el-table-column>
      <el-table-column prop="name" label="" align="center"></el-table-column>
      <el-table-column prop="priced" label="" align="center" width="80%"></el-table-column>
      <el-table-column prop="price" label="" align="center" width="80%"></el-table-column>
      <el-table-column prop="stock" label="" align="center" width="80%"></el-table-column>
      <el-table-column prop="cover" label="" width="80%" align="center">
        <template slot-scope="scope">
          <el-switch
            v-model="scope.row.cover"
            @change="changeCover(scope.row)"
            active-color="#13ce66"
            inactive-color="#C0CCDA"
            :active-value="1"
            :inactive-value="0"
          ></el-switch>
        </template>
      </el-table-column>
      <el-table-column prop="status" label="" width="140%" align="center">
        <template slot-scope="scope">
          <el-switch
            v-model="scope.row.status"
            @change="changeStatus(scope.row)"
            active-text=""
            inactive-text=""
            :active-value="1"
            :inactive-value="0"
          ></el-switch>
        </template>
      </el-table-column>
      <el-table-column label="" width="140%" align="center">
        <template slot-scope="scope">
          <el-button size="small" icon="el-icon-view" @click="handleView(scope.row)" circle></el-button>
          <el-button
            size="small"
            type="primary"
            icon="el-icon-edit-outline"
            @click="handleEdit(scope.row)"
            circle
          ></el-button>
          <el-button
            size="small"
            type="danger"
            icon="el-icon-delete"
            @click="handleDelete(scope.row)"
            circle
          ></el-button>
        </template>
      </el-table-column>
    </el-table>

    <!-- | -->
    <el-dialog title="" :visible.sync="editVisible">
      <el-form :model="form" :rules="editRules">
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="form.nickname" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth" prop="mobile">
          <el-input v-model="form.mobile" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth" prop="remain">
          <el-input v-model.number="form.remain" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="editVisible = false"> </el-button>
        <el-button type="primary" @click="saveEdit"> </el-button>
      </div>
    </el-dialog>

    <!--  -->
    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="queryInfo.pagenum"
      :page-sizes="[5, 10]"
      :page-size="queryInfo.pagesize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="total"
    ></el-pagination>
  </div>
</template>

<style>
.el-table .warning-row {
  background: oldlace;
}

.el-table .success-row {
  background: #f0f9eb;
}
</style>

<script>
export default {
  data() {
    return {
      // 
      editRules: {
        mobile: [
          { required: true, message: '' },
          { min: 11, max: 11, message: '11' }
        ],
        remain: [
          { required: true, message: '' },
          { type: 'number', message: '' }
        ]
      },
      // 
      queryInfo: {
        query: '',
        // 
        pagenum: 1,
        // 
        pagesize: 5
      },
      // 
      queryCategory: {
        query: '',
        pagenum: 1,
        pagesize: 5
      },
      // 
      queryStatus: {
        query: '',
        pagenum: 1,
        pagesize: 5
      },
      editVisible: false,
      form: {},
      formLabelWidth: '80px',
      res: [],
      total: 0,
      loading: true,
      categoryForm: {
        categoryList: [],
        name: ''
      },
      options: [
        {
          value: 1,
          label: ''
        },
        {
          value: 0,
          label: ''
        }
      ]
    }
  },
  watch: {
    $route() {
      // ...
      this.getFoodsList()
      this.getCategory()
    }
  },
  methods: {
    tableRowClassName({ rowIndex }) {
      if (rowIndex === 1) {
        return 'warning-row'
      } else if (rowIndex === 3) {
        return 'success-row'
      }
      return ''
    },
    // 
    filterbyCategory() {
      this.queryCategory.pagenum = this.queryInfo.pagenum
      this.queryCategory.pagesize = this.queryInfo.pagesize
      this.getFoodsLists(this.queryCategory)
    },
    // 
    filterbyStatus() {
      this.queryCategory.pagenum = this.queryInfo.pagenum
      this.queryCategory.pagesize = this.queryInfo.pagesize
      this.getFoodsLists(this.queryStatus)
    },
    async getCategory() {
      const path = this.URL.Category
      const temp = []
      const { data: res } = await this.$http.get(path).catch(() => {
        return this.$message.error('NETWORK ERROR')
      })
      res.forEach(element => {
        temp.push(JSON.parse(element))
      })
      this.categoryForm.categoryList = temp
    },
    // 
    getFoodsList() {
      this.getFoodsLists(this.queryInfo)
    },
    async getFoodsLists(query) {
      this.loading = true
      const path = this.URL.Foods
      let temp = []
      const { data: result } = await this.$http
        .get(path, {
          params: query
        })
        .catch(() => {
          this.$message.error('NETWORK ERROR')
        })
      this.loading = false
      console.log(result)
      result.foods.forEach(element => {
        temp.push(JSON.parse(element))
      })
      this.res = temp
      this.total = result.total
      console.log(this.res)
    },
    //  
    addFoods() {
      this.$router.push('/addfoods')
    },
    //  pagesize 
    handleSizeChange(newSize) {
      console.log(newSize)
      this.queryInfo.pagesize = newSize
      // 
      this.getFoodsList()
    },
    //   
    handleCurrentChange(newPage) {
      console.log(newPage)
      this.queryInfo.pagenum = newPage
      // 
      this.getFoodsList()
    },
    // 
    handleView(row) {
      this.$router.push({ name: 'ViewFoods', params: row })
    },
    // 
    handleEdit(row) {
      console.log(row)
      this.$router.push({ name: 'EditFoods', params: row })
    },
    // 
    async saveEdit() {
      this.editVisible = false
      const path = this.URL.Foods
      const { data: res } = await this.$http
        .patch(path, {
          data: {
            id: this.form.id,
            nickname: this.form.nickname,
            mobile: this.form.mobile,
            remain: this.form.remain
          }
        })
        .catch(() => {
          this.$message.error('NETWORK ERROR')
        })
      if (res.status !== 200) {
        this.$message.error('')
      }
      this.$message.success('')
    },
    // 
    async handleDelete(row) {
      const confirmResult = await this.$confirm(
        ', ?',
        '',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }
      ).catch(() => {
        this.$message.error('NETWORK ERROR')
      })

      console.log(confirmResult, row)
      if (confirmResult !== 'confirm') {
        return this.$message.info('')
      }

      // delete
      const path = this.URL.Foods
      const { data: res } = await this.$http
        .delete(path, {
          params: { id: row.id }
        })
        .catch(() => {
          this.$message.error('NETWORK ERROR')
        })
      if (res.status !== 200) {
        return this.$message.error('')
      }
      // 
      const path_delete = this.URL.Upload
      await this.$http
        .patch(path_delete, {
          data: {
            key: row.imageURL
          }
        })
        .catch(error => error)
      this.$message.success('')
      this.getFoodsList()
    },
    // 
    async changeStatus(row) {
      console.log(row.status)
      const path = this.URL.Foods
      const { data: res } = await this.$http
        .patch(path, {
          data: {
            id: row.id,
            status: row.status
          }
        })
        .catch(() => {
          this.$message.error('NETWORK ERROR')
        })
      if (res.status !== 200) {
        this.$message.error('!')
      }
      this.$message.success('')
    },
        // 
    async changeCover(row) {
      // console.log(row.status)
      const path = this.URL.Foods
      const { data: res } = await this.$http
        .patch(path, {
          data: {
            id: row.id,
            cover: row.cover
          }
        })
        .catch(() => {
          this.$message.error('NETWORK ERROR')
        })
      if (res.status !== 200) {
        this.$message.error('!')
      }
      this.$message.success('')
    }
  },
  created() {
    this.getFoodsList()
    this.getCategory()
  }
}
</script>

<style lang="less" scoped>
.state {
  text-align: center;
}
</style>