<template>
  <div>
    <!--  -->
    <el-card shadow="hover">
      <el-form>
        <el-row :gutter="5">
          <!--  -->
          <el-col :span="6">
            <el-select v-model="value" placeholder="">
              <el-option
                v-for="item in options"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              ></el-option>
            </el-select>
          </el-col>
          <!--  -->
          <el-col :span="8">
            <el-input placeholder="" v-model="input" clearable></el-input>
          </el-col>
          <!--  -->
          <el-col :span="4">
            <el-button type="primary"></el-button>
          </el-col>
        </el-row>
      </el-form>
    </el-card>
    <!--  -->
    <el-table :data="tableData" fit border :header-cell-style="{background:'#ffd0bd',color:'#606266'}">
      <el-table-column type="expand">
        <template slot-scope="props">
          <el-form label-position="left" inline class="demo-table-expand">
            <el-form-item label="">
              <span>{{ props.row.name }}</span>
            </el-form-item>
            <el-form-item label="">
              <span>{{ props.row.shop }}</span>
            </el-form-item>
            <el-form-item label="">
              <span>{{ props.row.id }}</span>
            </el-form-item>
            <el-form-item label=" ID">
              <span>{{ props.row.shopId }}</span>
            </el-form-item>
            <el-form-item label="">
              <span>{{ props.row.category }}</span>
            </el-form-item>
            <el-form-item label="">
              <span>{{ props.row.address }}</span>
            </el-form-item>
            <el-form-item label="">
              <span>{{ props.row.desc }}</span>
            </el-form-item>
          </el-form>
        </template>
      </el-table-column>
      <el-table-column label="" prop="id"></el-table-column>
      <el-table-column label="" prop="desc"></el-table-column>
      <el-table-column label="" prop="desc"></el-table-column>
      <el-table-column label=""></el-table-column>
      <el-table-column label=""></el-table-column>
    </el-table>

    <!--  -->
    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="queryInfo.pagenum"
      :page-sizes="[50, 100, 150, 200]"
      :page-size="queryInfo.pagesize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="res.length"
    ></el-pagination>
  </div>
</template>

<style>
.el-table .warning-row {
  background: oldlace;
}

.el-table .success-row {
  background: #f0f9eb;
}

.demo-table-expand {
  font-size: 0;
}
.demo-table-expand label {
  width: 90px;
  color: #99a9bf;
}
.demo-table-expand .el-form-item {
  margin-right: 0;
  margin-bottom: 0;
  width: 50%;
}
</style>

<script>
export default {
  data() {
    return {
      tableData: [
        {
          id: '12987122',
          name: '',
          category: '',
          desc: '',
          address: '',
          shop: '',
          shopId: '10333'
        },
        {
          id: '12987123',
          name: '',
          category: '',
          desc: '',
          address: '',
          shop: '',
          shopId: '10333'
        },
        {
          id: '12987125',
          name: '',
          category: '',
          desc: '',
          address: '',
          shop: '',
          shopId: '10333'
        },
        {
          id: '12987126',
          name: '',
          category: '',
          desc: '',
          address: '',
          shop: '',
          shopId: '10333'
        }
      ],
      // 
      queryInfo: {
        query: '',
        // 
        pagenum: 1,
        // 
        pagesize: 2
      },
      options: [
        {
          value: '1',
          label: ''
        },
        {
          value: '2',
          label: ''
        },
        {
          value: '3',
          label: ''
        }
      ],
      value: '',
      input: '',
      res: []
    }
  },
  methods: {
    tableRowClassName({ rowIndex }) {
      if (rowIndex === 1) {
        return 'warning-row'
      } else if (rowIndex === 3) {
        return 'success-row'
      }
      return ''
    },
    async getUserList() {
      const path = 'https://jsonplaceholder.typicode.com/todos'
      const { data: res } = await this.$http.get(path, result => {
        return result
      })
      this.res = res
      // console.log(res);
    },
    //  pagesize 
    handleSizeChange(newSize) {
      console.log(newSize)
      this.queryInfo.pagesize = newSize
      this.getUserList()
    },
    //   
    handleCurrentChange(newPage) {
      console.log(newPage)
      this.queryInfo.pagenum = newPage
      this.getUserList()
    }
  },
  created() {
    this.getUserList()
  }
}
</script>

<style lang="less" scoped>
.state {
  text-align: center;
}
</style>